<template>
    <div class="cu-top">
        <div>
            <el-row :gutter="20">
                <el-col :span="8">
                    <div class="grid-content bg-purple">
                        <el-card shadow="always" :body-style="{padding:'16px'}">
                            <el-row type="flex">
                                <el-col :span="12">
                                    <div class="">
                                        <svg class="top-icon" aria-hidden="true">
                                            <use xlink:href="#icon-gongzuohuibao"></use>
                                        </svg>
                                    </div>
                                </el-col>
                                <el-col :span="12" class="cu-row cu-blue">
                                    <div class="">
                                        七天预定数
                                    </div>
                                    <div class="">
                                        {{gloabData.todayReserver}}
                                    </div>
                                </el-col>
                            </el-row>
                        </el-card>
                    </div>
                </el-col>
                <el-col :span="8">
                    <div class="grid-content bg-purple">
                        <el-card shadow="always" :body-style="{padding:'16px'}">
                            <el-row type="flex">
                                <el-col :span="12">
                                    <div class="">
                                        <svg class="top-icon" aria-hidden="true">
                                            <use xlink:href="#icon-tiaodongshenqing"></use>
                                        </svg>
                                    </div>
                                </el-col>
                                <el-col :span="12" class="cu-row cu-origin">
                                    <div class="">
                                        用户数
                                    </div>
                                    <div class="">
                                        {{gloabData.allUser}}
                                    </div>
                                </el-col>
                            </el-row>
                        </el-card>
                    </div>
                </el-col>
                <el-col :span="8">
                    <div class="grid-content bg-purple">
                        <el-card shadow="always" :body-style="{padding:'16px'}">
                            <el-row type="flex">
                                <el-col :span="12">
                                    <div class="">
                                        <svg class="top-icon" aria-hidden="true">
                                            <use xlink:href="#icon-hetongqianding"></use>
                                        </svg>
                                    </div>
                                </el-col>
                                <el-col :span="12" class="cu-row cu-green">
                                    <div class="">
                                        待审核
                                    </div>
                                    <div class="">
                                        {{gloabData.toReview}}
                                    </div>
                                </el-col>
                            </el-row>
                        </el-card>
                    </div>
                </el-col>
            </el-row>
        </div>
        <div style="margin-top: 50px">
            <el-table
                    :stripe="true"
                    :data="tableData"
                    ref="multipleTable"
                    @select-all="handleSelect"
                    @select="handleSelect"
                    style="width: 100%"
                    max-height="450">
                <el-table-column
                        type="selection"
                        width="55">
                </el-table-column>
                <el-table-column
                        fixed
                        prop="datecreat"
                        label="创建"
                        :formatter="formatdatecreat"
                        width="150">
                </el-table-column>
                <el-table-column
                        label="借用房间"
                        width="150">
                    <template slot-scope="scope">
                        <i class="el-icon-location-outline"></i>
                        <span style="margin-left: 10px">{{ scope.row.roomData.room_name }}</span>
                    </template>
                </el-table-column>
                <el-table-column
                        label="借用日期"
                        width="150">
                    <template slot-scope="scope">
                        <i class="el-icon-date"></i>
                        <span style="margin-left: 10px">{{ scope.row.date}}</span>
                    </template>
                </el-table-column>
                <el-table-column
                        label="借用时间"
                        width="150">
                    <template slot-scope="scope">
                        <i class="el-icon-time"></i>
                        <span style="margin-left: 10px">{{`${CuformatDate(scope.row.start_time)} - ${CuformatDate(scope.row.end_time)}`}}</span>
                    </template>
                </el-table-column>
                <el-table-column
                        prop="name"
                        label="活动名称"
                        width="160">
                </el-table-column>
                <el-table-column
                        prop="event_intro"
                        label="活动介绍"
                        width="150">
                    <template slot-scope="scope">
                        <div>
                            <!--                            <div class="cu-table-jieduan">{{scope.row.event_intro}}</div>-->
                            <el-popover
                                    :offset="100"
                                    placement="top-start"
                                    width="200"
                                    trigger="hover"
                                    :content="scope.row.event_intro">
                                <span slot="reference" class="cu-blue">详情</span>
                            </el-popover>
                        </div>
                    </template>
                </el-table-column>
                <el-table-column
                        label="联系方式"
                        width="150">
                    <template slot-scope="scope">
                        <i :class="scope.row.contact_method | filterIco"></i>
                        <span style="margin-left: 10px">{{scope.row.contact_info}}</span>
                    </template>
                </el-table-column>
                <el-table-column
                        prop="belong"
                        label="所属组织"
                        width="150">
                </el-table-column>
                <el-table-column
                        label="操作"
                        mini-width="120">
                    <template slot-scope="scope">
                        <el-button
                                size="mini"
                                @click="handleReview(scope.$index,scope.row.id, 'allow')">通过
                        </el-button>
                        <el-button
                                size="mini"
                                type="danger"
                                @click="handleReview(scope.$index,scope.row.id,'reject')">拒绝
                        </el-button>
                    </template>
                </el-table-column>
            </el-table>
            <div style="margin-top: 20px;text-align: left;display: flex;">
                <el-button @click="handleReview(null,allSelectIdData,'allow')">通过</el-button>
                <el-button @click="handleReview(null,allSelectIdData,'reject')" type="danger">拒绝</el-button>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "GloabBoard",
        data() {
            return {
                gloabData: {
                    toReview: 0,
                    todayReserver: 0,
                    allUser: 0
                },
                tableData: null,
                allSelectIdData: [],
            }
        },
        created() {
            console.log('board');
            console.log(this.allSelectIdData===[]);
            this.$api.getGloabdata().then(res => {
                this.gloabData = res.data.gloabdata;
                this.tableData = res.data.reserverData;
            });

        },
        methods: {
            handleReview(index, id, type) {
                if(id.length===0){
                    this.$message.error('没有选中任何消息！请一定要选择哦！');
                    return
                }
                var data = {
                    type: type,
                    idlist: id
                };
                this.$api.reserverReview(data).then(res => {
                    if (res.data.status === 'success') {
                        console.log(index);
                        if (typeof index ==='number') {
                            this.tableData.splice(index, 1);
                            this.$notify({
                                title: '成功',
                                message: '1个预定审核成功',
                                type: 'success'
                            });
                        } else {
                            console.log(typeof id);
                            for (var x of id) {
                                this.tableData.splice(this.tableData.findIndex(item => {
                                    return item.id === x
                                }), 1)
                            }
                            this.$notify({
                                title: '成功',
                                message: id.length+'个预定审核成功',
                                type: 'success'
                            });
                        }
                    }
                });
                // this.tableData.splice(index, 1);
            },

            handleSelect(selection, row) {
                this.allSelectIdData = [];
                for (var sel of selection) {
                    this.allSelectIdData.push(sel.id);
                }
                console.log(this.allSelectIdData);
            },
            formatdatecreat(row, column, cellValue, index) {
                return this.$moment(cellValue).format("M月DD日 HH:mm");
            },
            /**
             * @return {string}
             */
            CuformatDate(value) {
                return this.$moment(value, 'HH:mm:ss').format("H:mm")
            },

        },
        computed: {},
        watch: {
            tableData() {
                this.$refs.multipleTable.clearSelection();
                this.allSelectIdData = [];
            }
        },
        filters: {
            filterIco(value) {
                if (value === 'qq') {
                    return 'iconfont icon-qq cu-blue'
                } else if (value === 'wx') {
                    return 'iconfont icon-weixin cu-green'
                } else {
                    return 'el-icon-mobile-phone cu-origin'
                }
            }
        }
    }
</script>

<style scoped>
    @import "../assets/cuico/iconfont.css";

    .top-icon {
        width: 3.6em;
        height: 3.6em;
        vertical-align: -0.15em;
        fill: currentColor;
        overflow: hidden;
    }

    .icon {
        width: 1em;
        height: 1em;
        vertical-align: -0.15em;
        fill: currentColor;
        overflow: hidden;
    }

    .cu-row {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
    }

    .cu-row div {
        flex: 1;
    }

    .cu-row div:nth-child(2) {
        font-weight: bold;
    }

    .cu-table-jieduan {
        display: -webkit-box;
        -webkit-box-orient: vertical;
        -webkit-line-clamp: 2;
        overflow: hidden;
    }

</style>